import React from 'react';
import Button from 'react-bootstrap/Button';

class Results extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }
  render() {
    let score;
    if (this.props.correctanswers === 0) {
      score = "Thanks for playing! You didn't get any questions right this time.";
    } else if (this.props.correctanswers === 1) {
      score = "Thanks for playing! You got 1 question right out of 5. You could've done worse!";
    } else if (this.props.correctanswers === 2) {
      score = "Thanks for playing! You got 2 questions right out of 5. Not great, but not bad either!";
    } else if (this.props.correctanswers === 3) {
      score = "Thanks for playing! You got 3 questions right out of 5. A respectable score.";
    } else if (this.props.correctanswers === 4) {
      score = "Thanks for playing! You got 4 questions right out of 5. Fantastic job!";
    } else {
      score = "Thanks for playing! You got 5 questions right out of 5. You're a geography genius!";
    }
    return (
      <div>
      <p>{score}</p>
      <Button variant="primary"
        className="playagain"
        onClick={this.props.newgame}>
        <p>Play again</p>
      </Button>
      </div>
    )
  }
}

export default Results;
