import React from 'react';
import Button from 'react-bootstrap/Button';

class Country2 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {

    }
  }
  render() {
    let country2;
    if (this.props.canchooseanswer === true) {
      country2 = <Button variant={this.props.choice2variant}
        onClick={this.props.selectCountry2}>
        {this.props.country2}
      </Button>
    } else {
      country2 = <Button variant={this.props.choice2variant}
      disabled>
        {this.props.country2}
      </Button>
    }
    return (
      <div>{country2}</div>
    )
  }
}

export default Country2;
