import React from 'react';
import Button from 'react-bootstrap/Button';

class Country1 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {

    }
  }
  render() {
    let country1;
    if (this.props.canchooseanswer === true) {
      country1 = <Button variant={this.props.choice1variant}
        onClick={this.props.selectCountry1}>
        {this.props.country1}
      </Button>
    } else {
      country1 = <Button variant={this.props.choice1variant}
        disabled>
        {this.props.country1}
      </Button>
    }
    return (
      <div>{country1}</div>
    )
  }
}

export default Country1;

