import React from 'react';
import Button from 'react-bootstrap/Button';

class Nextquestion extends React.Component {
  constructor(props) {
    super(props);
    this.state = {

    }
  }
  render() {
    let button;
    if (this.props.counter < 4) {
      button = <Button variant="info"
        size="lg"
        onClick={this.props.nextquestion}
        className="next">
        Next
      </Button>
    }
    else {
      button = <Button variant="primary"
        onClick={this.props.showresults}
        className="next"
        size="lg">
        See results
      </Button>
    }
    return (
      <div>
        {button}
      </div>
    )
  }
}

export default Nextquestion;
