import React from 'react';
import AChoseA from './AChoseA.js';
import AChoseB from './AChoseB.js';
import BChoseA from './BChoseA.js';
import BChoseB from './BChoseB.js';

class Answer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {

    }
  }
  render() {
    let aChoseA;
    let bChoseA;
    let aChoseB;
    let bChoseB;
    if (this.props.AChoseA) {
      aChoseA = <AChoseA
      populationdifference={this.props.populationdifference}
      country1={this.props.country1}
      country2={this.props.country2}
      />
    } else if (this.props.AChoseB) {
      aChoseB = <AChoseB
      populationdifference={this.props.populationdifference}
      country1={this.props.country1}
      country2={this.props.country2}
      />
    }
    else if (this.props.BChoseA) {
      bChoseA = <BChoseA
      populationdifference={this.props.populationdifference}
      country1={this.props.country1}
      country2={this.props.country2}
      />
    }
    else if (this.props.BChoseB) {
      bChoseB = <BChoseB
      populationdifference={this.props.populationdifference}
      country1={this.props.country1}
      country2={this.props.country2}
      />
    }
    return (
      <div className="answer">
        {aChoseA}
        {aChoseB}
        {bChoseA}
        {bChoseB}
      </div>
    )
  }
}

export default Answer;
